export default {
	components :{
		
	},
	data() {
		return {
			moldIndex: '0',
			moldArray: ['工单号', '联系人', '联系人电话','业务号','工单类型','状态','受理人','受理时间'],
			typeIndex: ' ',
			typeArray: [
				{
					id: '',
					name: '全部'
				},
				{
					id: 0,
					name: '承保类'
				},
				{
					id: 1,
					name: '待处理报案'
				},
				{
					id: 2,
					name: '电销类'
				},
				{
					id: 3,
					name: '救援反馈'
				},
			],
			stateIndex: ' ',
			stateArray: ['承保类', '待处理报案', '电销类', '救援反馈'], //状态
			date: '受理时间',
		}
	},
	
	onLoad() {

	},
	methods: {
		// 选择输入框的字段名称
		bindPickerChange(e) {
			this.moldIndex = e.detail.value
		},
		
		// 工单类型选项
		bindPickerType(e) {
			this.typeIndex = e.detail.value
		},
		
		// 状态
		bindPickerState(e) {
			this.stateIndex = e.detail.value
		},
		
		// 选择日期
		bindDateChange(e) {
			this.date = e.detail.value
		}
	}

	
}
